@import "../../styles/_variables.scss";

.pageNotFoundWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
  text-align: center;

  width: 100%;
  height: 100%;
  min-height: 84vh;

  background-color: $blue;
  padding-bottom: 25px;

  img {
    min-width: 50%;
    max-width: 100%;
    max-height: 60vh;
  }

  div {
    font-size: 24px;
    bottom: 0;

    a {
      margin-left: 5px;
      text-decoration: none;
      white-space: nowrap;
      color: cornflowerblue;
    }
  }
}